import React from 'react';
import { Route, Switch } from 'react-router-dom';

import SignInPage from './signInPage/signInPageContainer';
import Page404 from '../404/404Page/404Page';

const Auth = () =>
  (<div className='Auth'>
    <Switch>
      <Route path='/auth/sign-in' component={SignInPage} />
      <Route path='/' component={Page404} />
    </Switch>
  </div>);

export default Auth;



