// CONSTANTS
const errorMessages = {
  required: 'This field is required',
  passwordRequired: 'Password is requerd',
  emailError: 'Email is not valid',
  emailAndPositionRequired: 'Email and position are required fields',
};

export function emailValidator(value) {
  const re = /[A-Z0-9._%+-]+@[A-Z0-9.-]+.[A-Z]{2,4}/gim;
  const validEmail = value.match(re);
  return !!validEmail;
}

export default errorMessages;



