import React from 'react';
import { Route, Redirect } from 'react-router-dom';

import { connect } from 'react-redux';

import PropTypes from 'prop-types';

import isUserAuth from '../../selectors/userSelector';

// Show component only for logged in users
const AdminPrivateRoute = ({ component: Component, ...rest }) => {
  const { isUserAuthenticated, role } = rest;
  // prettier-ignore
  return (<Route
    {...rest}
    render={() =>
        (isUserAuthenticated && role === 'admin'
          ? <Component {...rest} />
          : <Redirect
            to={{
              pathname: rest.redirectPath,
            }}
          />)
      }
  />);
};

AdminPrivateRoute.propTypes = {
  component: PropTypes.func.isRequired,
  redirectPath: PropTypes.string.isRequired,
};

const mapStateToProps = state => ({
  isUserAuthenticated: isUserAuth(state),
  role: state.userState.user.role,
});

export default connect(mapStateToProps)(AdminPrivateRoute);



