import React from 'react';
import { Link } from 'react-router-dom';

const Page404 = () =>
  (<div className='Page404'>
    <div className='Page404-header'>
      <h2>Page not found</h2>
      <br />
      <Link to='/'> Home </Link>
    </div>
  </div>);

export default Page404;



