import { connect } from 'react-redux';
import SpecialistsPage from './SpecialistsPage';
import { createSpecialist, deleteSpesialist } from '../../../../store/reducers/specialist';
import { messageShow, messageHide } from '../../../../store/reducers/message';

const mapStateToProps = (state) => {
  const { specialistState: { specialists } } = state;
  return {
    specialists,
  };
};

const mapDispatchToProps = {
  createSpecialist,
  deleteSpesialist,
  messageShow,
  messageHide,
};

export default connect(mapStateToProps, mapDispatchToProps)(SpecialistsPage);



