import { connect } from 'react-redux';
import SpecialistsPageEdit from './SpecialistsPageEdit';
import { updateSpecialist } from '../../../../store/reducers/specialist';
import getSpecialistById from '../../../../selectors/specialisSelector';

const mapStateToProps = (state, ownProps) => {
  const id = ownProps.match.params.id;
  return {
    specialist: getSpecialistById(state, id),
  };
};

const mapDispatchToProps = {
  updateSpecialist,
};

export default connect(mapStateToProps, mapDispatchToProps)(SpecialistsPageEdit);



