import React from 'react';
import PropTypes from 'prop-types';

import AvatarImage from './AvatarImage';

const SpecialistInfoBlock = props =>
  (<div className='info-block'>
    <AvatarImage name={props.specialist.name} />
    <p>
      {props.specialist.name}
      <span>
        {props.specialist.position} {props.specialist.phone ? `, ${props.specialist.phone}` : ''}
      </span>
    </p>
  </div>);

SpecialistInfoBlock.propTypes = {
  specialist: PropTypes.object.isRequired,
};

export default SpecialistInfoBlock;



