import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Grid, Row, Col } from 'react-flexbox-grid';
import { CSSTransitionGroup } from 'react-transition-group';

import InfoBlock from '../../components/SpecialistInfoBlock';
import SearchField from '../../components/FormElements/SearchField/SearchField';
import NoResults from './NoResults';

class SpecialistsPage extends Component {
  state = {
    searchValue: '',
  };
  onFilterChange = (e) => {
    const searchValue = e.target.value;
    this.setState({
      searchValue,
    });
  };

  // filter specialists array by name
  filterSpecialists = () =>
    this.props.specialistsArray.filter(
      specialist => specialist.name.toUpperCase().indexOf(this.state.searchValue.toUpperCase()) !== -1,
    );

  render() {
    let specialists = this.props.specialistsArray;
    if (this.state.searchValue) {
      specialists = this.filterSpecialists();
    }
    const showSpecialistsList = this.props.specialistsArray.length > 0;
    return (
      <Grid fluid>
        <Row>
          <Col xs={12}>
            <h3>Specialists list</h3>
            {showSpecialistsList &&
              <div>
                <SearchField searchValue={this.state.searchValue} onFilterChange={this.onFilterChange} />
                <div className='specialists-list'>
                  <CSSTransitionGroup
                    transitionName='specialists-list'
                    component='div'
                    transitionEnterTimeout={500}
                    transitionLeaveTimeout={300}
                  >
                    {specialists.map(specialist =>
                      (<div key={specialist.id} className='specialists-list-item'>
                        <InfoBlock specialist={specialist} />
                      </div>),
                    )}
                  </CSSTransitionGroup>
                </div>
              </div>}
            {!showSpecialistsList && <NoResults />}
          </Col>
        </Row>
      </Grid>
    );
  }
}

SpecialistsPage.propTypes = {
  specialistsArray: PropTypes.array.isRequired,
};

export default SpecialistsPage;



