import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Page404 from '../404/404Page/404Page';
import SpecialistsPage from './Specialists/SpecialistListPage/SpecialistsPageContainer';
import SpecialistsPageEdit from './Specialists/SpesialistEditPage/SpecialistsEditContainer';

import './admin.css';

const Admin = () =>
  (<div className='container admin'>
    <Switch>
      <Route exact path='/admin/dashboard' component={SpecialistsPage} />
      <Route path='/admin/specialists/:id/edit' component={SpecialistsPageEdit} />
      <Route path='/' component={Page404} />
    </Switch>
  </div>);

export default Admin;



