import React from 'react';
import ReactDOM from 'react-dom';

// import createHistory from 'history/createBrowserHistory';
import { Route, Switch, BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';

import store from './store';
import Home from './routers/user/HomePageContainer';
import Admin from './routers/admin/Admin';
import Auth from './routers/auth';

import Page404 from './routers/404/404Page/404Page';
import registerServiceWorker from './registerServiceWorker';
import AdminPrivateRoute from './routers/admin/AdminPrivateRoute';
import Header from './containers/HeaderContainer';
import Notifications from './containers/NotificationsContainer';

import './index.css';

ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <div>
        <Header />
        <Switch>
          <Route exact path='/' component={Home} />
          <AdminPrivateRoute path='/admin' component={Admin} redirectPath='/auth/sign-in' />
          <Route path='/auth' component={Auth} />
          <Route path='/' component={Page404} />
        </Switch>
        <Notifications />
      </div>
    </BrowserRouter>
  </Provider>,
  document.getElementById('root'),
);
registerServiceWorker();



