import { connect } from 'react-redux';
import HomePage from './HomePage';
import { createSpecialist, deleteSpesialist } from '../../store/reducers/specialist';

const mapStateToProps = (state) => {
  const { specialistState: { specialists } } = state;
  const specialistsArray = Object.keys(specialists).map(id => specialists[id]);
  return {
    specialistsArray,
  };
};

const mapDispatchToProps = {
  createSpecialist,
  deleteSpesialist,
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);



