import { connect } from 'react-redux';

import { signIn } from '../../../store/reducers/user';
import { messageShow } from '../../../store/reducers/message';

import signInPageComponent from './signInPageComponent';
import isUserAuthenticated from '../../../selectors/userSelector';

const mapDispatchToProps = {
  signIn,
  messageShow,
};

const mapStateToProps = state => ({
  isUserAuthenticated: isUserAuthenticated(state),
  role: state.userState.user.role,
});

export default connect(mapStateToProps, mapDispatchToProps)(signInPageComponent);



