import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import Button from '../FormElements/Button/Button';

import './header.css';

// Component with information and actions for signed in user
const AdminLoggedInLinks = props =>
  (<div>
    <span>
      Welcome, {props.user.name}
    </span>
    <Button handleClick={props.logOut}>Log out</Button>
  </div>);

AdminLoggedInLinks.propTypes = {
  user: PropTypes.object.isRequired,
  logOut: PropTypes.func.isRequired,
};

// Component with actions for signed out user
const AdminLogOutLinks = () =>
  (<div>
    <Link to='/auth/sign-in'> Sign In </Link>
  </div>);

// Header component with navigation
const Header = ({ isUserAuthenticated, user, logOut }) =>
  (<div className='header'>
    <div className='left-header'>
      <Link to='/'>Home</Link>
      {isUserAuthenticated && <Link to='/admin/dashboard'>Dashboard</Link>}
    </div>
    <div className='right-header'>
      {!isUserAuthenticated && <AdminLogOutLinks />}
      {isUserAuthenticated && <AdminLoggedInLinks logOut={logOut} user={user} />}
    </div>
  </div>);

Header.propTypes = {
  isUserAuthenticated: PropTypes.bool.isRequired,
  logOut: PropTypes.func.isRequired,
  user: PropTypes.object,
};
Header.defaultProps = {
  user: {},
};

export default Header;



