import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Grid, Row, Col } from 'react-flexbox-grid';

import Button from '../../../../components/FormElements/Button/Button';
import InputFormField from '../../../../components/FormElements/InputFormField/InputFormField';
import AvatarImage from '../../../../components/AvatarImage';
import errorMessages from '../../../../helpers/validation';

const initSpecialistState = {
  name: '',
  position: '',
  phone: '',
};

class SpecialistForm extends Component {
  state = {
    specialist: this.props.specialist,
  };
  // set specialist field to component state
  onInputChange = (e) => {
    const { name, value } = e.target;
    const { specialist } = this.state;

    specialist[name] = value;
    this.setState({
      specialist,
    });
  };

  // validate specialist fields and send valid form
  addNewSpecialist = () => {
    const { specialist } = this.state;
    if (!specialist.name || !specialist.position) {
      this.props.messageShow(errorMessages.emailAndPositionRequired);
    } else {
      this.props.messageHide();
      this.props.submitForm(specialist);
      if (this.props.clearAfterSubmit) {
        // clear form
        this.clearForm();
      }
    }
  };
  // clear specialist state
  clearForm = () => {
    this.setState({
      specialist: Object.assign({}, initSpecialistState),
    });
  };

  render() {
    const { position, name, phone } = this.state.specialist;
    return (
      <Grid fluid>
        <Row>
          <Col xs={4} sm={3} md={4}>
            <AvatarImage name={name} style={{ width: '100%' }} />
          </Col>
          <Col xs={8} sm={9} md={8}>
            <InputFormField name='name' label='Name' onInputChange={this.onInputChange} value={name} />
            <InputFormField name='position' label='Position' onInputChange={this.onInputChange} value={position} />
            <InputFormField name='phone' label='Phone' onInputChange={this.onInputChange} value={phone} />
            <Button handleClick={this.addNewSpecialist} className='float-rigth'>
              Submit
            </Button>
          </Col>
        </Row>
      </Grid>
    );
  }
}

SpecialistForm.propTypes = {
  submitForm: PropTypes.func.isRequired,
  messageShow: PropTypes.func.isRequired,
  messageHide: PropTypes.func.isRequired,
  specialist: PropTypes.object,
  clearAfterSubmit: PropTypes.bool,
};
SpecialistForm.defaultProps = {
  specialist: Object.assign({}, initSpecialistState),
  clearAfterSubmit: false,
};

export default SpecialistForm;



