import React from 'react';
import PropTypes from 'prop-types';

import InputFormField from '../../../components/FormElements/InputFormField/InputFormField';
import Button from '../../../components/FormElements/Button/Button';

const SignInForm = props =>
  (<div>
    <InputFormField type='email' name='email' label='Email' onInputChange={props.onInputChange} value={props.email} />
    <InputFormField
      type='password'
      name='password'
      label='Password'
      onInputChange={props.onInputChange}
      value={props.password}
    />
    <Button handleClick={props.signIn}>Login</Button>
    <p>
      Use email <i>test@test.test</i> and password <i>test</i> to sign in to the test application
    </p>
  </div>);

SignInForm.propTypes = {
  signIn: PropTypes.func.isRequired,
  onInputChange: PropTypes.func.isRequired,
  email: PropTypes.string,
  password: PropTypes.string,
};

SignInForm.defaultProps = {
  email: '',
  password: '',
};

export default SignInForm;



