import React, { Component } from 'react';
import PropTypes from 'prop-types';

import logOutUserOnlyPage from '../../../HOC/logOutUserOnlyPage';
import SignInForm from './signInForm';
import validation, { emailValidator } from '../../../helpers/validation';

import './sign-in.css';

class SignInPage extends Component {
  state = {
    email: 'test@test.test',
    password: 'test',
  };
  onInputChange = (e) => {
    const { name, value } = e.target;
    this.setState({
      [name]: value,
    });
  };
  submitForm = () => {
    const { email, password } = this.state;
    const { messageShow, signIn } = this.props;

    const validEmail = emailValidator(email);

    if (!validEmail) {
      messageShow(validation.emailError);
    } else if (password) {
      signIn({ email, password });
    } else {
      messageShow(validation.passwordRequired);
    }
  };
  render() {
    return (
      <div className='sign-in-page'>
        <div className='sign-in-form'>
          <h2>Sign In Page</h2>
          <SignInForm
            signIn={this.submitForm}
            onInputChange={this.onInputChange}
            email={this.state.email}
            password={this.state.password}
          />
        </div>
      </div>
    );
  }
}
SignInPage.propTypes = {
  signIn: PropTypes.func.isRequired,
  messageShow: PropTypes.func.isRequired,
};

// component use logOutUserOnlyPage HOCs for check user log out state
export default logOutUserOnlyPage(SignInPage);



