import React, { Component } from 'react';
import PropTypes from 'prop-types';

// Check if user exists and check user role to redirect him to right route
function logOutUserOnlyPage(WrappedComponent) {
  return class extends Component {
    static propTypes = {
      history: PropTypes.object.isRequired,
      isUserAuthenticated: PropTypes.bool.isRequired,
      role: PropTypes.string,
    };
    static defaultProps = {
      role: '',
    };
    componentWillMount() {
      this.checkUser(this.props.isUserAuthenticated, this.props.role);
    }

    componentWillReceiveProps(nextProps) {
      this.checkUser(nextProps.isUserAuthenticated, nextProps.role);
    }

    checkUser = (isUserAuthenticated, role) => {
      if (isUserAuthenticated) {
        if (role === 'admin') {
          this.props.history.push('/admin/dashboard');
        } else {
          this.props.history.push('/');
        }
      }
    };
    render() {
      // Wraps the input component in a container, without mutating it. Good!
      return <WrappedComponent {...this.props} />;
    }
  };
}

export default logOutUserOnlyPage;



