import React from 'react';
import PropTypes from 'prop-types';
import { Grid, Row, Col } from 'react-flexbox-grid';
import { CSSTransitionGroup } from 'react-transition-group';

import SpecialistForm from '../SpecialistForm/SpecialistFormContainer';
import InfoBlock from '../../../../components/SpecialistInfoBlock';
import Button from '../../../../components/FormElements/Button/Button';
import LinkButtonStyle from '../../../../components/FormElements/Button/LinkButtonStyle';

import './specialists-list.css';

const SpecialistsPage = (props) => {
  const { specialists } = props;
  return (
    <div>
      <Grid>
        <Row>
          <Col xs={12}>
            <h3>Add new specialist</h3>
          </Col>
        </Row>
      </Grid>
      <SpecialistForm
        submitForm={props.createSpecialist}
        messageShow={props.messageShow}
        messageHide={props.messageHide}
        clearAfterSubmit
      />
      <Grid fluid>
        <Row>
          <Col xs={12}>
            <h3>Specialists list</h3>
            <div className='specialists-list'>
              <CSSTransitionGroup
                transitionName='specialists-list'
                component='div'
                transitionEnterTimeout={500}
                transitionLeaveTimeout={300}
              >
                {Object.keys(specialists).reverse().map(id =>
                  (<div key={id} className='specialists-list-item'>
                    <InfoBlock specialist={specialists[id]} />
                    <div className='actions'>
                      <LinkButtonStyle linkTo={`/admin/specialists/${id}/edit`}>Edit</LinkButtonStyle>
                      <Button className='danger' handleClick={() => props.deleteSpesialist(id)}>
                        Delete
                      </Button>
                    </div>
                  </div>),
                )}
              </CSSTransitionGroup>
            </div>
          </Col>
        </Row>
      </Grid>
    </div>
  );
};

SpecialistsPage.propTypes = {
  specialists: PropTypes.object.isRequired,
  createSpecialist: PropTypes.func.isRequired,
  messageShow: PropTypes.func.isRequired,
  messageHide: PropTypes.func.isRequired,
};

export default SpecialistsPage;



