import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import './button.css';

// Add additional wrap for a Link component
const LinkButtonStyle = props =>
  (<div className={`button-wrapper ${props.className}`}>
    <div className='inner-wrapper'>
      <Link className='btn hover-effect-btn' to={props.linkTo}>
        {props.children}
      </Link>
    </div>
  </div>);

LinkButtonStyle.propTypes = {
  linkTo: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
  className: PropTypes.string,
};

LinkButtonStyle.defaultProps = {
  className: '',
};

export default LinkButtonStyle;



