import React, { Component } from 'react';
import PropTypes from 'prop-types';
import LinkButtonStyle from '../../../../components/FormElements/Button/LinkButtonStyle';

import SpecialistForm from '../SpecialistForm/SpecialistFormContainer';

class SpecialistsPage extends Component {
  componentWillReceiveProps() {
    // redirect after submit form
    this.props.history.push('/admin/dashboard');
  }

  render() {
    return (
      <div>
        <LinkButtonStyle linkTo={'/admin/dashboard'} className='back-btn'>
          ❮ Go Back
        </LinkButtonStyle>
        <SpecialistForm submitForm={this.props.updateSpecialist} specialist={this.props.specialist} />
      </div>
    );
  }
}

SpecialistsPage.propTypes = {
  updateSpecialist: PropTypes.func.isRequired,
  specialist: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};

export default SpecialistsPage;



